<template>
  <div id="app">
    <div class="app-phone">
      <div class="phone-header">
        <a 
          class="cancel-cta"
          @click="goToHome"
          v-if="step === 2 || step ===3">ÂèñÊ∂à</a>
        <img src="./assets/vue_gram_logo_cp.png" alt="">
        <a 
          class="next-cta"
          v-if="step === 2"
          @click="step++">‰∏ã‰∏ÄÊ≠•</a>
        <a 
          class="next-cta"
          v-if="step === 3"
          @click="share">ÂèëË°®</a>
      </div>
      <PhoneBody 
        :posts="posts" 
        :filters="filters"
        :step="step"
        :image="image"
        :selectFilter="selectFilter"
        v-model="caption">
        </PhoneBody>
      <div class="phone-footer">
        <div class="home-cta">
          <i class="fa fa-home" @click="goToHome"></i>
        </div>
        <div class="upload-cta">
          <input type="file" id="file" name="file" @input="uploadImage" :disabled="step !== 1">
          <label for="file">
            <i class="fa fa-plus-square-o"></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PhoneBody from "./components/PhoneBody"
import posts from "./data/posts.js" // Êï∞ÊçÆ
import filters from "./data/filters" //Êª§ÈïúÂêçÂ≠ó
import eventBus from "./event-bus" // Âè¶‰∏Ä‰∏™vueÂÆû‰æã

export default {
  name: 'app',
  created() {
    eventBus.$on("selectedFilter", evt => {
      this.selectFilter = evt;
    })
  },
  data() {
    return {
      posts,    // Êï∞ÊçÆ
      filters,   // Êª§ÈïúÂêçÂ≠ó
      step: 1,    // ÊâÄÂú®È°µÈù¢
      image: "", // Ë¶Å‰∏ä‰º†ÁöÑÂõæÁâá
      selectFilter: "",// Êª§Èïú
      caption: "",  // ËØ¥Êòé
    }
  },
  methods: {
    uploadImage(e) {
      const files = e.target.files;
      if(!files.length) return; // Â¶ÇÊûúÈÄâÊã©‰∫Ü‰∏Ä‰∏™Êñá‰ª∂ÔºåÂÜçÂèñÊ∂àÁöÑËØùÈÄÄÂá∫
      // Êñá‰ª∂Ë¢´ÈÄâ‰∏≠ÁªßÁª≠Êìç‰Ωú
      // ËØªÂèñÊñá‰ª∂ËΩ¨Âåñ‰∏∫data64Ê†ºÂºèÂπ∂Ëß¶Âèëonload‰∫ã‰ª∂
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = evt => {
        this.image = evt.target.result;
        this.step = 2;
      }
    },
    goToHome() {
      this.image = "";
      this.selectFilter = "";
      // ÂèñÊ∂àÈÄâÊã©ÂõæÁâáÊó∂ÂÄô‰∏ä‰∏ÄÊ¨°Êª§ÈïúÁöÑÈÄâÊã©
      this.step = 1; // Âéª‰∏ªÈ°µ
    },
    share() {
      const post = {
        username: "fanghuayong",
        userImage: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg",
        postImage: this.image,
        likes: 0,
        hasBeenLiked: false,
        caption: this.caption,
        filter: this.selectFilter,
      }
      this.posts.unshift(post);
      this.goToHome();// ÊèíÂÖ•ÂêéËøîÂõûÂà∞È¶ñÈ°µ
    }
  },
  components: {
    PhoneBody,
  }
}
</script>

